.mentor {
  padding-top: 60px;
  padding-bottom: 80px;
}

.mentor__top {
  max-width: 551px;
  margin-bottom: 50px;
}

.mentor__item {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mentor__item__block {
  cursor: pointer;
  border-radius: 6px;
  flex: 0 32%;
  transition: all .3s;
  box-shadow: 0 11px 55px #00000026;
}

.mentor__item__block:hover {
  margin-top: -15px;
  box-shadow: 0 5px 20px #0003;
}

.mentor__item__block__image {
  height: 340px;
  border-radius: 6px 6px 0 0;
  overflow: hidden;
}

.mentor__item__block__image img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.mentor__item__block__info {
  padding: 16px 32px 39px;
}

.mentor__item__block__info__name {
  color: #222;
  margin-bottom: 8px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 31px;
}

.mentor__item__block__info__post {
  color: #555;
  margin-bottom: 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
}

.mentor__item__block__info__extra {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mentor__item__block__info__extra__rate {
  max-width: 100%;
}

.mentor__item__block__info__extra__rate img:first-child {
  margin-right: 8px;
}

.mentor__item__block__info__extra__amount {
  color: #555;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

@media (max-width: 1080px) {
  .mentor__item__block__image {
    height: 280px;
  }
}

@media (max-width: 890px) {
  .mentor__item__block__image {
    height: 340px;
  }

  .mentor__item {
    justify-content: center;
  }

  .mentor__item__block {
    flex: 0 48%;
    margin-bottom: 25px;
    margin-right: 15px;
  }

  .mentor__item__block__info {
    padding: 16px 32px 19px;
  }

  .mentor {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media (max-width: 780px) {
  .mentor__item__block__image {
    height: 320px;
  }

  .mentor__item__block {
    flex: 0 48%;
    margin-right: 15px;
  }

  .mentor__item__block:first-child {
    display: none;
  }

  .mentor__item__block__info__name {
    margin-bottom: 7px;
    font-size: 20px;
    line-height: 22px;
  }

  .mentor__item__block__info__post {
    margin-bottom: 15px;
  }
}

@media (max-width: 620px) {
  .mentor__item__block {
    flex: 0 70%;
    margin-right: 0;
  }

  .top {
    max-width: 420px;
  }

  .mentor {
    padding-bottom: 30px;
  }
}

@media (max-width: 440px) {
  .mentor__item__block {
    flex: 0 90%;
  }

  .mentor__item__block__image {
    height: 320px;
  }
}

